<template>
  <aside class="side-menu">
    <button v-on:click="toggleLanguage()" v-tooltip="tooltipConfiguration(english ? 'po polsku' : 'po angielsku')">
      <document-text class="small">
        <strong v-if="!english" style="padding-left: 4px">en</strong>
        <strong v-if=" english" style="padding-left: 3px">pl</strong>
      </document-text>
    </button>

    <button v-on:click="print()" v-tooltip="tooltipConfiguration(english ? 'print' : 'drukuj')">
      <i class="fas fa-download" style="font-size: 24px"></i>
    </button>

    <button v-on:click="clear()" v-tooltip="tooltipConfiguration(english ? 'clear changes' : 'usuń zmiany')">
      <i class="fas fa-times" style="font-size: 28px"></i>
    </button>
  </aside>
</template>

<script>
export default {
  name: 'SideMenu',
  data: function () {
    const query = this.$route.query

    return {
      english: query.english === 'true'
    }
  },
  methods: {
    tooltipConfiguration: function (content) {
      return {
        content,
        offset: 20,
        placement: 'right'
      }
    },
    toggleLanguage: function () {
      const query = this.$route.query

      this.english = !this.english
      this.$router.push({ query: { ...query, english: this.english ? 'true' : 'false' } })
    },
    print: function () {
      window.print()
    },
    clear: function () {
      this.$router.push({ query: { english: this.english ? 'true' : 'false' } })
      this.$router.go(this.$router.currentRoute)
    }
  }
}
</script>

<style scoped>
  @media print {
    .side-menu { display: none !important; }
  }

  .side-menu {
    position: fixed;
    top: 80px;
    left: 30px;
  }

  button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    border: 0;
    border-radius: 2px;
    width: 60px;
    height: 60px;
    line-height: 32px;
    text-transform: uppercase;
    color: #ddd;
    border: 2px solid #eee;
    background-color: transparent;
    outline-color: transparent;
    cursor: pointer;
    transition: 0.25s;
    text-align: center;
    margin-bottom: 20px;
  }

  button:hover {
    color: #222;
    border-color: #666;
  }
</style>



