<template>
  <header>
    <document-text class="big">
      <span style="margin-left: -1px">{{ english ? 'Invoice' : 'Faktura VAT' }}</span>
      <url-input class="input-big" name="title" v-model="title" />
    </document-text>

    <document-text class="small">
      <url-input class="input-small" name="subtitle" v-model="subtitle" />
    </document-text>
  </header>
</template>

<script>
import moment from 'moment'

const getTitle = (query, english) => query.title || `FV ${moment().format('M/YYYY')}`
const getSubtitle = (query, english) => query.subtitle || (english ? 'Original' : 'Kopia')

export default {
  name: 'Titles',
  props: ['english'],
  data: function () {
    const query = this.$route.query

    return {
      title: getTitle(query, this.english),
      subtitle: getSubtitle(query, this.english)
    }
  },
  beforeUpdate: function () {
    const query = this.$route.query

    this.title = getTitle(query, this.english)
    this.subtitle = getSubtitle(query, this.english)
  }
}
</script>

<style scoped>
  header {
    display: flex;
    flex-direction: column;
  }

  .small {
    margin-top: 10px;
  }

  .input-big {
    width: 300px;
    display: inline;
    line-height: 32px;
    font-size: 22pt;
    letter-spacing: 4px;
    font-family: 'Montserrat';
  }

  .input-small {
    display: inline;
    line-height: 32px;
    font-family: 'Montserrat';
    font-weight: 400;
    letter-spacing: 5px;
    font-size: 12px;
    text-transform: uppercase;
  }
</style>



