<template>
  <section class="col-group sides">
    <div class="col-6">
      <document-text class="big">{{ english ? 'Seller' : 'Sprzedawca' }}:</document-text>
      <space />
      <div class="vertical">
        <document-text>
          <strong>Maciej Mazur Tu i Teraz</strong>
        </document-text>
        <document-text>
          <strong>{{ english ? 'Perkoza street 15/13' : 'ul. Perkoza 15/13' }}</strong>
        </document-text>
        <document-text>
          <strong>{{ english ? '44-100, Gliwice, Poland' : '44-100, Gliwice' }}</strong>
        </document-text>
      </div>

      <space />
      <div class="col-group">
        <div class="col-4">
          <document-text>{{ english ? 'EU Identifier' : 'NIP EU' }}</document-text>
        </div>
        <div class="col-8">
          <url-input type="text" name="sellerId" v-model="seller.id" />
        </div>
        <div class="col-4">
          <document-text>Bank</document-text>
        </div>
        <div class="col-8">
          <url-input type="text" name="sellerBank" v-model="seller.bank" />
        </div>
        <div class="col-4">
          <document-text>{{ english ? 'Bank account' : 'Konto bankowe' }}</document-text>
        </div>
        <div class="col-8">
          <url-input type="text" name="sellerBankAccount" v-model="seller.bankAccount" />
        </div>
      </div>
    </div>

    <div class="col-6">
      <document-text class="big">{{ english ? 'Buyer' : 'Nabywca' }}:</document-text>
      <space />
      <div class="vertical">
        <url-input type="text" name="buyerLine1" v-model="buyer.line1" />
        <url-input type="text" name="buyerLine2" v-model="buyer.line2" />
        <url-input type="text" name="buyerLine3" v-model="buyer.line3" />
      </div>

      <space />
      <div class="col-group">
        <div class="col-4">
          <document-text>{{ english ? 'EU Identifier' : 'NIP EU' }}</document-text>
        </div>
        <div class="col-8">
          <url-input type="text" name="buyerId" v-model="buyer.id" />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
const getBank = (query, english) => query.sellerBank || (english ? 'ING Silesian Bank' : 'ING Bank Śląski')
const getLine1 = (query, english) => query.buyerLine1 || (english ? 'Some company' : 'Jakaś firma')
const getLine2 = (query, english) => query.buyerLine2 || (english ? 'Some street' : 'Jakaś ulica')
const getLine3 = (query, english) => query.buyerLine3 || (english ? 'Some postcode, city, country' : 'Jakiś kod pocztowy, miasto, kraj')

export default {
  name: 'Sides',
  props: ['english'],
  data: function () {
    const query = this.$route.query

    return {
      seller: {
        id: query.sellerId || '1234 4312 4325 5325 6234 PL',
        bank: getBank(query, this.english),
        bankAccount: query.sellerBankAccount || '123 123 123 123 123 123'
      },
      buyer: {
        line1: getLine1(query, this.english),
        line2: getLine2(query, this.english),
        line3: getLine3(query, this.english),
        id: query.buyerId || '1234 1234 1234 1234 1234 EU'
      }
    }
  },
  beforeUpdate: function () {
    const query = this.$route.query

    this.seller.bank = getBank(query, this.english)
    this.buyer.line1 = getLine1(query, this.english)
    this.buyer.line2 = getLine2(query, this.english)
    this.buyer.line3 = getLine3(query, this.english)
  },
  methods: {
    onChange: function (field, value) {
      this.$router.push({ query: { ...this.$route.query, [field]: value } })
    }
  }
}
</script>

<style scoped>
  .vertical {
    display: flex;
    flex-direction: column;
  }
</style>



