<template>
  <input type="text" v-bind:value="formatted" v-bind:placeholder="placeholder"
         v-on:change="onChange($event.target.value)" />
</template>

<script>
import { querize, format, modelize, getPlaceholder } from './mappers'

export default {
  name: 'UrlInput',
  props: ['name', 'type', 'value'],
  methods: {
    onChange: function (value) {
      const output = modelize(this.type, value)

      this.$emit('input', output)
      this.$router.push({ query: { ...this.$route.query, ...querize(this.type, this.name, value) } })
    }
  },
  computed: {
    formatted: function () {
      return format(this.type, this.value)
    },
    placeholder: function () {
      return getPlaceholder(this.type)
    }
  }
}
</script>

<style scoped>
  input {
    border: 0;
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 100%;
    font-weight: 700;
    font-size: 13pt;
    line-height: 32px;
    font-family: 'Source Sans Pro';
    background-color: transparent;
  }

  input:focus {
    outline-color: #a58d52;
  }
</style>



