<template>
  <section class="subjects">
    <table>
      <tr>
        <th style="width:  6%">{{ english ? 'No.' : 'Nr' }}</th>
        <th>{{ english ? 'Service name' : 'Usługa' }}</th>
        <th style="width:  8%">{{ english ? 'Amount' : 'Ilość' }}</th>
        <th style="width:  8%">{{ english ? 'Unit' : 'J.m.' }}</th>
        <th style="width: 15%">{{ english ? 'Price netto' : 'Cena netto' }}</th>
        <th style="width: 15%">{{ english ? 'Value netto' : 'Wartość netto' }}</th>
        <th style="width:  6%">%</th>
        <th style="width: 15%">Vat</th>
        <th style="width: 15%">{{ english ? 'Value brutto' : 'Wartość brutto' }}</th>
      </tr>

      <tr>
        <td>1.</td>
        <td>{{ english ? 'Consultation' : 'Konsultacje' }}</td>
        <td><url-input class="input" type="number" name="amount" v-model="amount" /></td>
        <td>{{ english ? 'hour' : 'godz.' }}</td>
        <td><url-input class="input" type="currency" name="priceNetto" v-model="priceNetto" /></td>
        <td>{{ valueNetto | currency(currencySettings) }}</td>
        <td><url-input class="input" type="number" name="vatPercentage" v-model="vatPercentage" /></td>
        <td>{{ vat | currency(currencySettings) }}</td>
        <td>{{ valueBrutto | currency(currencySettings) }}</td>
      </tr>

      <tr>
        <td class="blank" colspan="4"></td>
        <td class="blank">{{ english ? 'total' : 'razem' }}:</td>
        <td>{{ valueNetto | currency(currencySettings) }}</td>
        <td></td>
        <td>{{ vat | currency(currencySettings) }}</td>
        <td>{{ valueBrutto | currency(currencySettings) }}</td>
      </tr>

      <tr>
        <td class="blank" colspan="4"></td>
        <td class="blank">VAT:</td>
        <td>{{ valueNetto | currency(currencySettings) }}</td>
        <td>{{ vatPercentage }}</td>
        <td>{{ vat | currency(currencySettings) }}</td>
        <td>{{ valueBrutto | currency(currencySettings) }}</td>
      </tr>
    </table>

    <space class="big" />
    <div class="col-group">
      <div class="col-12">
        <document-text class="big">
          <span>{{ english ? 'Total value' : 'Razem'}}:</span>
          <strong v-bind:title="zlotowki | currency(polishCurrency)">{{ valueBrutto | currency(currencySettings) }}</strong>
        </document-text>
      </div>

      <div class="col-12">
        <document-text>
          <span v-bind:style="{ 'padding-left': english ? '137px' : '61px' }">{{ english ? 'Verbally' : 'Słownie' }} :</span>
          <span style="padding-left: 12px">{{ priceVerbally | capitalize }}.</span>
        </document-text>
      </div>
    </div>
  </section>
</template>

<script>
import converters from './converters'

const getDecimal = split => {
  if (!split) return null
  if (split.length === 1) return split * 10
  return +split.substr(0, 2)
}

const getPriceNetto = (query, english) => {
  return {
    amount: query.priceNettoAmount || 10,
    currency: query.priceNettoCurrency || (english ? 'EUR' : 'zł')
  }
}

export default {
  name: 'Subjects',
  props: ['english'],
  data: function () {
    const query = this.$route.query

    return {
      vatPercentage: query.vatPercentage || 0,
      amount: query.amount || 160,
      priceNetto: getPriceNetto(query, this.english),
      polishCurrency: { symbol: 'zł', fractionSeparator: ',' }
    }
  },
  beforeUpdate: function () {
    const query = this.$route.query
    const expected = getPriceNetto(query, this.english)

    this.priceNetto.amount = expected.amount
    this.priceNetto.currency = expected.currency
  },
  computed: {
    currencySettings: function () {
      const isPriceInPLN = this.priceNetto.currency === 'zł'

      return {
        symbol: isPriceInPLN ? 'zł' : 'EUR',
        thousandsSeparator: ' ',
        fractionCount: 2,
        fractionSeparator: isPriceInPLN ? ',' : '.',
        symbolPosition: 'end',
        symbolSpacing: true
      }
    },
    priceVerbally: function () {
      const isPriceInPLN = this.priceNetto.currency === 'zł'
      const converter = this.english ? converters.en : converters.pl

      const string = `${Math.round(this.valueBrutto * 100) / 100}`
      const splits = string.split('.')

      const euro = splits[0]
      const cent = getDecimal(splits[1])

      const translations = {
        joiner: this.english ? 'and' : 'i',
        smaller: isPriceInPLN ? 'złotych' : (this.english ? 'cents' : 'centów'),
        bigger: isPriceInPLN ? 'złotych' : 'euro'
      }

      const euroString = `${converter.toWords(euro)} ${translations.bigger}`
      const centString = cent > 0 ? ` ${translations.joiner} ${converter.toWords(cent)} ${translations.smaller}` : ''

      return `${euroString}${centString}`
    },
    valueNetto: function () {
      return this.priceNetto.amount * this.amount
    },
    vat: function () {
      return Math.round(this.valueNetto * (this.vatPercentage / 100))
    },
    valueBrutto: function () {
      return this.valueNetto + this.vat
    },
    zlotowki: function () {
      return this.valueBrutto * 4.3
    }
  },
  methods: {
    onChange: function (field, value) {
      this.$router.push({ query: { ...this.$route.query, [field]: value } })
    }
  }
}
</script>

<style scoped>
  table {
    width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
  }

  th {
    background-color: #eee;
    border-bottom: 1px solid #ccc;
  }

  td:not(.blank) {
    font-weight: 700;
    background-color: #faf9f9;
  }

  td.blank {
    background-color: transparent;
    text-align: right;
    opacity: 0.75;
  }

  input,
  th,
  td {
    margin: 0;
    padding: 10px 15px;

    white-space: nowrap;
    text-align: left;
    font-family: monospace;
    font-size: 14px;
    font-weight: normal;
  }

  .input {
    border: 0;
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 100%;
    font-weight: 700;
    line-height: 16px;
  }
</style>



