<template>
  <span class="document-text"><slot></slot></span>
</template>

<script>
export default {
  name: 'DocumentText'
}
</script>

<style scoped>
  .document-text {
    font-weight: 400;
    font-size: 13pt;
    line-height: 32px;
    font-family: 'Source Sans Pro';
  }

  .document-text.big {
    font-weight: 300;
    font-size: 22pt;
    letter-spacing: 4px;
    font-family: 'Montserrat';
  }

  .document-text.small {
    font-family: 'Montserrat';
    font-weight: 400;
    letter-spacing: 5px;
    font-size: 12px;
  }

  .document-text.uppercase {
    text-transform: uppercase;
  }
</style>



