<template>
  <div id="app">
    <router-view />
    <mazur-fake-loader>
      <side-menu />
    </mazur-fake-loader>
  </div>
</template>

<script>
import SideMenu from './components/SideMenu'

export default {
  name: 'App',
  components: {
    SideMenu
  }
}
</script>

<style>
  #app {
    box-sizing: border-box;
    padding: 40px;
    min-height: 100vh;
    /*background-color: #eee;*/
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  @media print {
    .tooltip { display: none !important; }
  }

  .tooltip {
    z-index: 99999;
    background-color: #222;
    color: white;
    border-radius: 2px;
    font-family: 'Montserrat';
    font-weight: 300;
    font-size: 12px;
    letter-spacing: 2px;
    text-transform: uppercase;
    line-height: 24px;
  }

  .tooltip[aria-hidden='true'] {
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.25s, visibility 0.25s;
  }

  .tooltip[aria-hidden='false'] {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.25s;
  }

  .tooltip-inner {
    padding: 5px 18px;
    padding-right: 15px;
  }

  .tooltip-arrow {
    position: relative;
    background: #222;
  }

  .tooltip-arrow:after {
    border: solid transparent;
    content: '';
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(0, 0, 0, 0);
    border-width: 6px;
  }

  [x-placement="right"] .tooltip-arrow:after {
    right: 100%;
    top: 50%;
    margin-top: -6px;
    border-right-color: black;
  }
</style>



