<template>
  <mazur-fake-loader class="container">
    <div class="col-group">
      <div class="col-8">
        <titles v-bind:english="english"></titles>

        <space v-bind:english="english" />
        <basics v-bind:english="english" />
      </div>
      <div class="col-4">
        <div class="logo-container">
          <logo v-bind:english="english"></logo>
        </div>
      </div>
    </div>

    <space class="big" />
    <sides v-bind:english="english"></sides>

    <space class="big" />
    <subjects v-bind:english="english" />
  </mazur-fake-loader>
</template>

<script>
import Logo from './areas/logo'
import Titles from './areas/titles'
import Sides from './areas/sides'
import Basics from './areas/basics'
import Subjects from './areas/subjects'

export default {
  name: 'Document',
  props: ['english'],
  components: {
    Logo,
    Titles,
    Sides,
    Basics,
    Subjects
  }
}
</script>

<style scoped>
  .container {
    box-sizing: border-box;
    max-width: 1050px;
    padding: 40px;
    background-color: white;
  }

  .logo-container {
    margin-top: -3px;
    height: 90px;
    display: flex;
    justify-content: flex-end;
  }
</style>



