import moment from 'moment'

const dateFormat = 'DD-MM-YYYY'
const datePattern = /[0-9][0-9]-[0-9][0-9]-[0-9][0-9][0-9][0-9]/

const currencyPlaceholder = '10 EUR'
const currencyPattern = /[0-9]*[ ][zł|EUR]/

const datePatternToMoment = pattern => {
  const parts = pattern.split(/-/g)

  return moment({
    date: parts[0],
    month: parts[1] - 1,
    year: parts[2]
  })
}

const modelize = (type, value) => {
  if (type === 'date') {
    return datePattern.test(value) ? datePatternToMoment(value) : null
  }

  if (type === 'currency') {
    if (currencyPattern.test(value)) {
      const parts = value.split(' ')

      return {
        amount: +parts[0],
        currency: parts[1]
      }
    } else {
      return 0
    }
  }

  return value
}

const querize = (type, name, value) => {
  if (type === 'date') {
    return { [name]: value }
  }

  if (type === 'currency') {
    const model = modelize(type, value)

    return {
      [`${name}Amount`]: model.amount,
      [`${name}Currency`]: model.currency
    }
  }

  return { [name]: modelize(type, value) }
}

const format = (type, value) => {
  if (type === 'date' && value) {
    const isDatePattern = datePattern.test(value)

    return isDatePattern ? value : value.format(dateFormat)
  }

  if (type === 'currency') {
    return `${value.amount} ${value.currency}`
  }

  return value
}

const getPlaceholder = (type) => {
  if (type === 'number') {
    return dateFormat
  }

  if (type === 'currency') {
    return currencyPlaceholder
  }

  return ''
}

export {
  querize,
  format,
  modelize,
  dateFormat,
  getPlaceholder
}



