<template>
  <section class="basics">
    <div class="col-group">
      <div class="col-3">
        <document-text>{{ english ? 'Issue location' : 'Miejscowość' }}</document-text>
      </div>
      <div class="col-9">
        <url-input name="location" v-model="location" />
      </div>

      <div class="col-3">
        <document-text>{{ english ? 'Date of issue' : 'Data wystawienia' }}</document-text>
      </div>
      <div class="col-9">
        <url-input type="date" name="issuedAt" v-model="issuedAt" />
      </div>

      <div class="col-3">
        <document-text>{{ english ? 'Date of provision' : 'Data sprzedaży' }}</document-text>
      </div>
      <div class="col-9">
        <url-input type="date" name="provisionedAt" v-model="provisionedAt" />
      </div>
    </div>
  </section>
</template>

<script>
import moment from 'moment'

const getLocation = (query, english) => query.location || (english ? 'Gliwice, Poland' : 'Gliwice')

export default {
  name: 'Basics',
  props: ['english'],
  data: function () {
    const expectedAt = moment().set({ date: 20 })
    const query = this.$route.query

    return {
      location: getLocation(query, this.english),
      issuedAt: query.issuedAt || moment(expectedAt),
      provisionedAt: query.provisionedAt || moment(expectedAt)
    }
  },
  beforeUpdate: function () {
    const query = this.$route.query

    this.location = getLocation(query, this.english)
  }
}
</script>

<style scoped>
  .col-3 * {
    white-space: nowrap;
  }
</style>



