import Vue from 'vue'
import Router from 'vue-router'
import Document from '@/components/Document'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Document',
      component: Document,
      props: route => ({ ...route.query, english: route.query.english === 'true' }),
      beforeEnter: (to, from, next) => {
        if (!to.query.english) {
          const query = { ...to.query, english: to.query.english || 'true' }

          next({ path: '/', query, replace: true })
        }

        next()
      }
    }
  ]
})

export default router



